    border-radius: 20px;
    margin: 10px;
    padding: 10px;
    display: flex;
    justify-content: space-around;
    img{
        width: 50px;
    }
    #cartText{
        font-size: 15px;
    }
    #pizzaInfo{
        #pizzaAddition{
            width: 50px;
            height: 20px;
        }
    }
}